<template>
  <div style="">
    <h3 v-if="this.$route.path!='/preview'&&this.$route.path!='/clockinpreview'&&this.$route.path.indexOf('/answer')==-1">
      <span style="color: red">* </span>
      {{ question.title }}
    </h3>
    <p>{{ question.description}}</p>
    <div class="text-center">
      <v-dialog
          v-model="dialog"
          width="500"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-text-field  style="width:20vw;" dense
                         solo @input="update" v-model="question.answer" @click = "getLocation" color="red lighten-2"
                         v-bind="attrs"
                         v-on="on"
          ></v-text-field>
        </template>

        <v-card>
          <v-card-title class="text-h5 grey lighten-2">
            是否允许系统获取您的当前位置?
          </v-card-title>

          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
                color="primary"
                text
                @click="permit"
            >
              允许
            </v-btn>
            <v-btn
                color="primary"
                text
                @click="denial"
            >
              禁止
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
  </div>
</template>

<script>
import "@/assets/font/font.css"
export default {
  name: "FillLocation",
  props:{
    value: {
      type: Object,
    },
  },
  mounted(){
    this.question=this.value
  },
  model: {
    prop: "value",
    event: "selection",
  },
  data(){
    return{
      question:{},
      dialog: false,
    }
  },
  methods:{
    update() {
      this.$emit("selection",this.question);
      //console.log(this.choice);
    },
    getLocation(){
      this.$jsonp('https://apis.map.qq.com/ws/location/v1/ip', {
        key: "3WVBZ-JLV65-CIIIO-QGEIC-FS5R3-OYF2R",
        output: "jsonp",
      })
          .then(res => {
            var str = JSON.stringify(res.result.ad_info);
            var loc = eval("(" + str + ")");
            console.log(res);
            this.question.answer = loc.nation + "." + loc.province + "." + loc.city + "." +loc.district;
          })
          .catch(error => {
            console.log(error);
          });
    },
    permit(){
      this.dialog = false;
      this.getLocation()
    },
    denial(){
      this.dialog = false;
      this.question.answer = "未知"
    }
  }
}
</script>
