<template>
  <v-app style="background-color: rgb(247, 247, 247);" id="demo">
    <v-dialog    v-model="dialog"  width="500" height="400">
      <v-card>
        <v-card-title class="text-h5 primary lighten-2">
          答案
        </v-card-title>
        <v-card-text>
            <h4 style="margin-top:2vh;">正确答案:{{answers[answerIndex].correctAnswer}}</h4>
            <h4 style="margin-top:2vh;">你的答案:{{answers[answerIndex].myAnswer}}</h4>
            <h4 style="margin-top:2vh;">该题得分:{{answers[answerIndex].score}}</h4>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary"  text  @click="dialog=false">
            确认
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <div class="result" v-if="status==0">
            <img src="../../assets/paper.png" alt="" style="margin-top:15%;margin-left:45%;">
            <h1 style="margin-top:1%;margin-left:44%;">您已成功提交</h1>
            <v-btn color="primary"  width='10vw' height="5vh"
             style="margin-top:1%;margin-left:45%;" @click="status=1">查看答卷结果</v-btn>
        </div>
    <div v-if="status==1">
      <div style="text-align:center;font-size:30px;margin-top:30px;font-family:'bgtitlefamily';"> {{ title }} </div>
      <div style="margin: 20px 200px;">
        <v-card>
          <v-card-text style="text-align:center;
       font-size:20px;margin-top:20px;padding-top:20px;font-family:'bgtitlefamily';">同学你的得分是:{{score}}</v-card-text>
          <div v-for="(item, index) in question" :key="index" style="margin:5px 40px;">
            <div v-if="index == 0" style="height:20px;"></div>
            <!-- 题目标号 -->
            <div style="font-size:20px;position:relative;top:10px;">
              <span style="color:red;float:left;" v-if="item.necessary==true || item.necessary=='true'">* </span>
              {{ (page-1)*4+index + 1 + '. ' }} {{ item.title }}
            </div>
            <v-btn small color="primary"  @click="showAnswer((page-1)*4+index+1)"
            style="float:right;margin-right:30vw;margin-top:-2vh;">查看答案</v-btn>
            <p style="margin-top:1vh;margin-left:1.5vw;">{{ item.description}}</p>
            <!-- 多选题、多选投票题、多选报名题 -->
            <div v-if="item.type == '多选题'">
              <v-container>
              <div v-for="(subItem, i) in item.choices"
                    :key="i">
                <v-checkbox v-model="item.Answer" :label="subItem" hide-details
                :value="subItem"
                >
                <template v-slot:label>
                  <div  style="width:30vw;">
                    {{subItem}}
                  </div>
                </template>
                </v-checkbox>
              </div>
            </v-container>
            </div>
            <!-- 单选题、单选投票题、单选报名题 -->
            <div v-if="item.type == '单选题'" >
              <v-container>
                <v-radio-group v-model="item.answer">
                  <div v-for="(subItem, i) in item.choices" :key="i">
                    <v-radio :value="subItem" style="height:40px;">
                      <template v-slot:label>
                        <div  style="width:30vw;">
                          {{ subItem }}
                        </div>
                      </template>
                    </v-radio>
                  </div>
                </v-radio-group>
              </v-container>
            </div>
            <!-- 填空题 -->
            <div v-if="item.type == '填空题'">
              <div style="display:inline-block;width:400px;margin-left:20px;margin-top:20px;">
                <v-text-field
                    v-model="item.answer"
                    label="请输入你的答案"
                    clearable
                ></v-text-field>
              </div>
            </div>
            <!-- 文本题 -->
            <div v-if="item.type == '文本题'">
              <div style="display:inline-block;width:400px;margin-left:20px;margin-top:20px;">
                <v-textarea style="width:30vw;margin-left:0vw;margin-top:1vh;" dense
                    v-model="item.answer"
                    label="请输入你的答案"
                    clearable
                ></v-textarea>
              </div>
            </div>
          </div>
          <!-- 翻页 -->
          <div style="text-align:center;">
            <v-btn color="warning" @click="lastPage"
                  style="width:100px;margin:30px 2vw 2vh 2vw;color:white;">上一页</v-btn>
            <v-btn color="warning" @click="nextPage"
                  style="background-color:skyblue;width:100px;margin:30px 2vw 2vh 2vw;color:white;">下一页</v-btn>
          </div>
        </v-card>
      </div>
    </div>
  </v-app>
</template>

<script>
import "@/assets/font/font.css";
import htmlToPdf from '@/plugins/htmlToPdf';
import Header from '@/components/Header';
import FillLocation from "@/components/FillBlank/FillLocation";
export default {
  name: 'Preview',
  components: {
      Header,FillLocation
  },
  data() {
      return {
          title: '这是一个调查',
          dialog:false,
          status:0,
          page:1,
          score:0,
          totalPage:1,
          answerIndex:0,
          paperDescription:'这是一个调查的问卷简的问卷是一个调查的问卷简介',
          id:-1,
          answers:[''],
          questions:[],
          question: [
          ],
      }
  },
  mounted(){
    this.id=this.$route.params.id
    this.getData()
  },
  methods: {
    check(item) {
      console.log(item);
    },
    showAnswer(index){
      this.answerIndex=index-1
      this.dialog=true
    },
    getData(){
      this.$axios({url:'/answer/get',method:'get',params:{
        id:this.id,
         user_id:parseInt(localStorage.getItem("id")),
      }})
        .then((res) => {
          console.log(res);
          if(res.data.status==true) {
            this.questions=res.data.question
            this.title=res.data.title
            this.paperDescription=res.data.paperDescription
             if(this.questions.length%4==0) this.totalPage=this.questions.length/4
            else if(this.questions.length%4!=0) this.totalPage=parseInt(this.questions.length/4)+1
            for(var i=0;i<4&&i<this.questions.length;i++)  this.question.push(this.questions[i])
            console.log(this.questions);
          }
          else this.$message.error({ type: "error", message: res.data.exec });
        })
        .catch((err) => {
          console.log(err);
          this.$message.error({ type: "error", message: "网络错误" });
        });
        this.$axios({url:'/exam/result',method:'get',params:{
        id:this.id,
        user_id:parseInt(localStorage.getItem("id")),
      }})
        .then((res) => {
          console.log(res);
          if(res.data.status==true) {
            this.answers=res.data.answer
            for(var i=0;i<this.answers.length;i++)  this.score+=this.answers[i].score
          }
          else this.$message.error({ type: "error", message: res.data.exec });
          console.log(this.question,this.answers,111);
        })
        .catch((err) => {
          console.log(err);
          this.$message.error({ type: "error", message: "网络错误" });
        });
    },

    lastPage(){
        if(this.page==1) {
            this.$message.warning('这已经是第一页了')
        }
        else{
            this.question=[]
            this.page--
            for(var i=(this.page-1)*4;i<this.page*4&&i<this.questions.length;i++){
                this.question.push(this.questions[i])
            }
        }
    },
    nextPage(){
      if(this.page==this.totalPage) {
          this.$message.warning('这已经是最后一页了')
      }
      else{
          this.question=[]
          this.page++
          for(var i=(this.page-1)*4;i<this.page*4&&i<this.questions.length;i++){
              this.question.push(this.questions[i])
          }
      }
    },
  }
}
</script>
<style scoped>
.result{
    background-color: rgb(242, 242, 242);
    width: 100%;
    height:100%;
}
</style>